/* 6. Write CSS rules that cause the header and .collage div to be centered on the page */

header,
#root {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.collage {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}
img {
  padding: 1em;
}
